.vacShort{
	position: relative;
	width: 100%;
	min-height: 20px;
	padding-top: 20px;
	padding-bottom: 20px;
	border-bottom: 1px dotted #cccccc;

}

.vacHeader{
	display: block;
	font-weight: bold;
	padding-bottom: 10px;
	text-decoration: none;
	color: #000000;
}

.vacHeader:hover{
	text-decoration: underline;
}

.vacSalary{
	font-size: 14px;
	color: #666666;
	padding-bottom: 10px;
}

.vacDesc{
	font-size: 12px;
	line-height: 1.3;
}


.noBold{
	font-weight: normal;
	color: #666666
}


